export const create_invoice = async (invoiceData: any) => {
  const url = 'https://apisandbox.facturama.mx/3/cfdis';

  const headers = {
    'Content-Type': 'application/json',
    'User-Agent': 'cjaviergar',
    'Authorization': 'Basic Y2phdmllcmdhcjpHYXJjMjAxOT8=' // Reemplaza con el token real
  };

  const body = {
    NameId: "1", // Valor fijo
    Currency: "MXN", // Valor fijo
    CfdiType: "I", // Valor fijo (Ingreso)
    Exportation: "01", // Valor fijo (No aplica exportación)
    PaymentForm: invoiceData.PaymentForm, // Forma de pago proporcionada por el usuario
    PaymentMethod: invoiceData.PaymentMethod, // Método de pago proporcionado por el usuario
    ExpeditionPlace: invoiceData.ExpeditionPlace, // Lugar de expedición proporcionado por el usuario
    Date: invoiceData.Date, // Fecha de emisión proporcionada por el usuario
    Receiver: {
      Rfc: invoiceData.Receiver.Rfc, // RFC del receptor proporcionado por el usuario
      CfdiUse: invoiceData.Receiver.CfdiUse, // Uso del CFDI proporcionado por el usuario
      Name: invoiceData.Receiver.Name, // Nombre del receptor proporcionado por el usuario
      FiscalRegime: invoiceData.Receiver.FiscalRegime, // Régimen fiscal del receptor proporcionado por el usuario
      TaxZipCode: invoiceData.Receiver.TaxZipCode // Código postal del receptor proporcionado por el usuario
    },
    Items: invoiceData.Items // Lista de productos proporcionada por el usuario
  };

  try {
    const response = await fetch(url, {
      method: 'POST',
      headers: headers,
      body: JSON.stringify(body),
    });

    if (!response.ok) {
      throw new Error(`Error: ${response.statusText}`);
    }

    const invoice = await response.json();
    return invoice;
  } catch (error) {
    console.error('Error creating invoice:', error);
    return null;
  }
};

export const get_customer = async (keyword: string) => {
    const url = `https://apisandbox.facturama.mx/Client?keyword=${keyword}`;
  
    const headers = {
      'Content-Type': 'application/json',
      'User-Agent': 'cjaviergar',
      'Authorization': 'Basic Y2phdmllcmdhcjpHYXJjMjAxOT8=' // Reemplaza con el token real
    };
  
    try {
      const response = await fetch(url, {
        method: 'GET',
        headers: headers,
      });
  
      if (!response.ok) {
        throw new Error(`Error: ${response.statusText}`);
      }
  
      const customers = await response.json();
      return customers;
    } catch (error) {
      console.error('Error fetching customers:', error);
      return null;
    }
  };
  
export const get_product = async (keyword?: string) => {
    const url = keyword 
      ? `https://apisandbox.facturama.mx/Product?keyword=${keyword}` 
      : 'https://apisandbox.facturama.mx/Product';
  
    const headers = {
      'Content-Type': 'application/json',
      'User-Agent': 'cjaviergar',
      'Authorization': 'Basic Y2phdmllcmdhcjpHYXJjMjAxOT8=' // Reemplaza con el token real
    };
  
    try {
      const response = await fetch(url, {
        method: 'GET',
        headers: headers,
      });
  
      if (!response.ok) {
        throw new Error(`Error: ${response.statusText}`);
      }
  
      const products = await response.json();
      return products;
    } catch (error) {
      console.error('Error fetching products:', error);
      return null;
    }
  };

